<template>
  <div class="embed-responsive embed-responsive-16by9">
    <iframe
      @load="load"
      class="embed-responsive-item"
      src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d13135.13808738704!2d-58.3795567!3d-34.6096099!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x95bccad10638ec91%3A0xcde18020281e0a94!2sFiscal%C3%ADa%20de%20la%20Ciudad%20-%20Fiscal%C3%ADa%20de%20C%C3%A1mara%20CAyT!5e0!3m2!1sen!2sar!4v1584217906852!5m2!1sen!2sar"
    ></iframe>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  created() {
    this.SET_TITLE("Ubicación");
  },
  mounted() {
    this.loader = this.$loading.show();
  },
  beforeRouteLeave(to, from, next) {
    // Oculta el loader antes de cambiar de pestaña
    this.loader.hide();
    next();
  },
  methods: {
    ...mapMutations(["SET_TITLE"]),
    load() {
      console.log("Loaded...");
      this.loader.hide();
    }
  }
};
</script>

