<template>
  <div class="embed-responsive embed-responsive-16by9">
    <iframe
      @load="load"
      class="embed-responsive-item"
      src="https://calendar.google.com/calendar/embed?height=600&amp;wkst=1&amp;bgcolor=%23ffffff&amp;ctz=America%2FArgentina%2FBuenos_Aires&amp;src=anV6Z2FkbzEwcGN5ZkBnbWFpbC5jb20&amp;src=ZXMtNDE5LmFyI2hvbGlkYXlAZ3JvdXAudi5jYWxlbmRhci5nb29nbGUuY29t&amp;color=%23039BE5&amp;color=%230B8043&amp;showTz=0&amp;showCalendars=0&amp;showPrint=0&amp;showTitle=0"
    ></iframe>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  created() {
    this.SET_TITLE("Agenda");
  },
  mounted() {
    this.loader = this.$loading.show();
  },
  beforeRouteLeave(to, from, next) {
    // Oculta el loader antes de cambiar de pestaña
    this.loader.hide();
    next();
  },
  methods: {
    ...mapMutations(["SET_TITLE"]),
    load() {
      console.log("Loaded...");
      this.loader.hide();
    }
  }
};
</script>

